@import"https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@300;500;700&family=Orbitron:wght@400;700&display=swap";:root{--erotic-pink: #ff0077;--detective-cyan: #00ffff;--bg-dark: #10001a;--bg-panel: #2a0a3a;--border-color: rgba(255, 0, 119, 0.3);--text-light: #f5f5f5;--text-dim: #b0a0c0;--aq-green: #44ff00;--aq-orange: #ff8000;--aq-gold: #ffcc00;--font-main: 'Noto Sans SC', sans-serif}.hud-header{display:flex;flex-direction:column;align-items:center;gap:8px;text-align:center}.hud-block-title{color:var(--detective-cyan);font-family:var(--font-hud);margin:0}.hud-phase{color:var(--text-light);font-family:var(--font-hud);margin:0}.skill-upgrade-grid{display:grid;grid-template-columns:repeat(3, 1fr);gap:15px;margin-top:15px}.skill-card{background:linear-gradient(145deg, var(--bg-dark), var(--bg-panel));border:1px solid var(--border-color);border-radius:6px;padding:15px;display:flex;flex-direction:column;transition:all .3s ease;position:relative;overflow:hidden;cursor:pointer}.skill-upgrade-grid:not(.selecting) .skill-card:hover{transform:translateY(-5px);border-color:var(--detective-cyan);box-shadow:0 0 15px rgba(0,255,255,.3)}.skill-upgrade-grid.selecting .skill-card{pointer-events:none;opacity:.6}.skill-card-header{font-family:var(--font-hud);text-align:center;margin-bottom:15px}.skill-title{font-size:1.25rem;color:var(--detective-cyan);text-shadow:var(--cyan-glow);margin:0;padding-bottom:5px;border-bottom:1px dashed rgba(0,255,255,.4);text-transform:uppercase}.skill-meta{display:flex;justify-content:space-between;font-size:.8rem;margin-top:8px;color:var(--text-dim);text-transform:uppercase}.skill-quality,.skill-nature{padding:2px 6px;border-radius:3px;font-weight:700}.skill-card-body{font-family:var(--font-main);color:var(--text-light);font-size:.9rem;line-height:1.6;flex-grow:1;white-space:pre-wrap}@media(max-width: 800px){.skill-upgrade-grid{grid-template-columns:1fr;gap:12px}.hud-header{flex-direction:column;align-items:flex-start;gap:8px}.skill-card{padding:12px}.skill-title{font-size:1.1rem}}